<template>
  <div class="HisReplay">
    <!-- <el-button type="primary" @click="tdx_15_to_day" size="small">tdx 15</el-button>
    <el-button type="primary" @click="tbj_15_to_day" size="small">tbj 15</el-button>
    <el-button type="primary" @click="jjsj_15_to_day" size="small">jjsj 15</el-button>-->
    <el-button type="primary" @click="xgsj_5_to_day" size="small">xgsj 5</el-button>
    <el-button type="primary" @click="save_data" size="small">csv</el-button>
    <el-button type="primary" @click="get_day_obj_list_csv" size="small">day_vibrate </el-button>
    <br/>
    <div>
      <h4></h4>
      <p>30</p>
      <p>60</p>
      <p></p>
      <p></p>
    </div>
    <el-date-picker
      v-model="selected_day"
      type="date"
      class="small_width"
      placeholder="">
    </el-date-picker>
    <br/>
    <el-button type="primary" @click="forecast_before_10" size="small">10</el-button>
    <el-button type="primary" @click="forecast_after_10" size="small">10</el-button>
    <br/>
    <div>
      <div>5{{day_5_for_val}}</div>
      <div>1: {{forecast1}}  -- 2{{forecast2}}</div>
       <br/>
       <span> </span>
      <el-switch v-model="is_up_or_down"></el-switch>
      <br/>
      <span>{{is_up_or_down?'':''}}:</span>
      <el-input v-model="input_low_or_high" placeholder="" class="small_width"></el-input>
      <div>5{{target0}} --: {{target1}}  -- {{target2}}</div>
    </div>
  </div>
</template>
<script>
import moment from "moment"
import k_handel_API from '@/utils/k_handel.js';
import common_API from '@/utils/common.js';
import csv_API from '@/utils/csv.js';
import m15_to_day_Mixin from "./m15_to_day_Mixin.js";
import forecast_Mixin from "./forecast_Mixin.js";
const Big = require('big.js')
export default {
  name: 'HisReplay',
  mixins: [m15_to_day_Mixin, forecast_Mixin],
  data() {
    return {
      symbol:'MAM',
      ma_period: 222,//2221234;//5
      today: new Date(),
      selected_day: new Date('2012/10/10'), //  2012/9/20
      selected_day_obj: null,
      past_list: [],
      day_5_for_val: 0,
      day_obj_list:[],
      forecast1:0,
      forecast2:0,
      forecast_aver:0,
      for12_R:0,
      is_up_or_down:true,
      input_low_or_high:0,
      h1_vibrate_range_list:[],
      h1_vol_range_list:[],
    };
  },
  computed: {
    target0: function () {
      let val = 0;
      if(this.input_low_or_high){
        if(this.is_up_or_down){//
          val = +this.input_low_or_high + this.day_5_for_val;
        }else{//
          val = +this.input_low_or_high - this.day_5_for_val;
        }
      }
      return common_API.toFixed(val,2);
    },
    target1: function () {
      let val = 0;
      if(this.input_low_or_high){
        if(this.is_up_or_down){//
          val = +this.input_low_or_high + this.forecast1;
        }else{//
          val = +this.input_low_or_high - this.forecast1;
        }
      }
      return common_API.toFixed(val,2);
    },
    target2: function () {
      let val = 0;
      if(this.input_low_or_high){
        if(this.is_up_or_down){//
          val = +this.input_low_or_high + this.forecast2;
        }else{//
          val = +this.input_low_or_high - this.forecast2;
        }
      }
      return common_API.toFixed(val,2);
    }
  },
  mounted () {
    common_API.longLog();
    //this.get_p64_list()
    this.get_day_obj_list_csv();
    //this.get_p49_list();
  },
  methods: {
    get_day_obj_list_csv(){
      let save_file_name = 'xgsj_MAM_day_obj.csv'
      csv_API.read_csv(save_file_name, false,',',null,['h1_vol','h1_vibrate','day_vibrate','day_high','day_low']).then((res)=>{
        console.log('day_obj res1:', common_API.copyObj(res));
        this.day_obj_list = res;
        //this.statistics(res)
        this.forecast_before_10();
        this.forecast_after_10();
      }).catch((err) => {
        console.error('get_his_data err:', err);
      })
    },
    forecast_before_10(){
      console.log('this.selected_day:', new Date(this.selected_day).toLocaleDateString());
      let selected_day_idx = -1;
      let len = this.day_obj_list.length, item;
      for(let i=0; i<len; i++){
        item = this.day_obj_list[i];
        if(item.day === new Date(this.selected_day).toLocaleDateString()){
          selected_day_idx = i;
          break;
        }
      }
      if(selected_day_idx >=0){
        this.select_day_obj = this.day_obj_list[selected_day_idx];
        this.past_list = this.day_obj_list.slice(0, selected_day_idx)
        console.log('select_day_obj', common_API.copyObj(this.select_day_obj));
        console.log('past_day_list', common_API.copyObj(this.past_list));
        this.day_5_forecast(this.past_list);
      }else{
        console.log('');
        this.$message.error('');
      }
    },
    forecast_after_10(){
      let h1_vibrate = this.select_day_obj.h1_vibrate;
      let h1_vol = this.select_day_obj.h1_vol;
      let day_vibrate = this.select_day_obj.day_vibrate;
      let day = new Date(this.selected_day).toLocaleDateString()
      console.log(' h1_vibrate:', h1_vibrate, ' h1_vol:', h1_vol);
      this.handle_h1_forecast(h1_vibrate, h1_vol, this.past_list, day_vibrate, day);
      // this.h1_vol_range_list = this.deal_hz_vol(past_list, 25000)
      // this.h1_vibrate_range_list = this.deal_hz_vibrate(past_list)
    },
    statistics(res){
      console.log('ma_period:', this.ma_period);
      let item, list, day, h1_vibrate, h1_vol, day_vibrate;
      let for_diff, for12_R;
      let num= new Big(0), sum1 = new Big(0), sum2 = new Big(0);
      let len = res.length;
      for(let i = 80; i<85; i++){
        item = res[i];
        day = item.day;
        h1_vibrate = item.h1_vibrate;
        h1_vol = item.h1_vol;
        day_vibrate = item.day_vibrate;
        list = res.slice(0, i);
        //console.log('i', common_API.copyObj(res[i]));
        ({ for_diff, for12_R } = this.handle_h1_forecast(h1_vibrate, h1_vol, list, day_vibrate, day));
        num = num.plus(1);
        sum1 = sum1.plus(Math.abs(for_diff));
        sum2 = sum2.plus(Math.abs(for12_R));
      }
      Big.DP = 2
      let f_aver = sum1.div(num);
      let R_aver = sum2.div(num);
      console.log('num:', num.toFixed(0), ' f_aver:', f_aver.toFixed(2), ' R_aver:', R_aver.toFixed(2));
    },
    get_p49_list(){
      let save_file_name = 't2_book_p49_list.csv'
      csv_API.read_csv(save_file_name, false,',',null,['day_vibrate','h1_vibrate','h1_vol']).then((res)=>{
        console.log('get_p49_list res1:', common_API.copyObj(res));
        this.handle_h1_forecast(77, 68184, res);
      }).catch((err) => {
        console.error('get_p49_list err:', err);
      })
    },
    handle_h1_forecast(h1_vibrate, h1_vol, k_list, real=0, day){
      //console.log('10past_day_list:', common_API.copyObj(k_list));
      let h1_vol_list=[], h1_vibrate_list=[];
      let h1_vol_diff, h1_vibrate_diff, h1_vol_diff_abs, h1_vibrate_diff_abs;
      k_list.forEach((item)=>{
        h1_vol_diff = item.h1_vol - h1_vol;
        h1_vol_diff_abs = Math.abs(h1_vol_diff)
        h1_vibrate_diff = item.h1_vibrate - h1_vibrate;
        h1_vibrate_diff_abs = Math.abs(h1_vibrate_diff)
        h1_vol_list.push({...item, h1_vol_diff, h1_vol_diff_abs})
        h1_vibrate_list.push({...item, h1_vibrate_diff, h1_vibrate_diff_abs})
      })
      this.forecast1 = this.handle_h1_vibrate_list(h1_vibrate_list)
      this.forecast2 = this.handle_h1_vol_list(h1_vol_list)
      let sum = common_API.toFixed(this.forecast1 + this.forecast2,2);
      this.forecast_aver = common_API.toFixed(sum/2,2);
      this.for12_R = common_API.toFixed(Math.abs((this.forecast1 - this.forecast_aver)),2);
      console.log('day:', day, '\tr1:', this.forecast1, '\tr2:', this.forecast2, '\taver', this.forecast_aver, '\tR:', this.for12_R ,);
      let for_diff = common_API.toFixed((this.forecast_aver - real),2)
      //console.log('day:', day, '\tr1:', this.forecast1, '\tr2:', this.forecast2, '\tR:', this.for12_R , '\taver', this.forecast_aver, '\treal:', real, '\tdiff:', for_diff);
      return
    },
    get_p64_list(){
      let save_file_name = 't2_book_p64_list.csv'
      csv_API.read_csv(save_file_name, false,',',null,['day_vibrate']).then((res)=>{
        console.log('get_p64_list res1:', common_API.copyObj(res));
        this.deal_day_vibrate(res);
      }).catch((err) => {
        console.error('get_p64_list err:', err);
      })
    },
  }
}
</script>
<style scoped>
.echarts-view{
  height: 300px;
  width:  100%;
}
.small_width{
  width: 100px;
}
</style>